jB;;EAEA;IACE,qBAAqB;EACvB;EACA;IACE,sBAAsB;EACxB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,iBAAiB;IACjB,uBAAuB;EACzB;;EAEA;IACE,UAAU;IACV,gBAAgB;EAClB;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,aAAa;IACb,qCAAqC;EACvC;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,sBAAsB;EACxB;;EAEA;IACE,iBAAiB;IACjB,sBAAsB;EACxB;AACF;AACA,WAAW;AACX;EACE;IACE,cAAc;EAChB;;EAEA;IACE,oBAAoB;EACtB;;EAEA;IACE,aAAa;IACb,0BAA0B;EAC5B;;EAEA;IACE,oBAAoB;EACtB;;EAEA;IACE,oBAAoB;EACtB;AACF;AACA,2EAA2E","sourcesContent":["/* Fonts */\n@import url(\"https://fonts.googleapis.com/css2?family=Inter:wght@100;300;400;700&family=Reenie+Beanie&display=swap&family=Monoton&display=swap\");\n\n:root {\n  --bg-secondary: #c2b38c;\n  --bg-primary: #e0dbc7;\n  --transition: all 350ms ease;\n  --border-radius: 2rem;\n  --bg-black: #001524;\n  --bg-contrast: #ffb65d;\n  --shadow: 5px 5px 0 0 rgba(0, 0, 0, 1);\n  --bg-danger: #bf1722;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n  outline: none;\n  border: none;\n  text-decoration: none;\n}\n\nhtml {\n  scroll-behavior: smooth;\n}\n\nbody {\n  font-family: \"Inter\", sans-serif;\n  background-color: var(--bg-primary);\n  color: #fff;\n}\n\n.skip-link {\n  position: absolute;\n  top: -50px; /* Sembunyikan tautan dari tampilan */\n  left: 0;\n  background-color: var(--bg-danger);\n  color: #fff;\n  padding: 1.3rem 0.5rem;\n  z-index: 100;\n}\n\n.skip-link:focus {\n  top: 0; /* Tampilkan tautan saat fokus diberikan */\n}\n\nsection {\n  min-height: 100vh;\n}\n\n.navbar {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  background-color: var(--bg-primary);\n  border-bottom: 1px solid var(--bg-black);\n  position: fixed;\n  top: 0;\n  left: 0;\n  right: 0;\n  z-index: 99;\n}\n\n.navbar .navbar__brand {\n  font-family: \"Monoton\", cursive;\n  font-size: 1.5rem;\n  font-weight: 700;\n  color: var(--bg-black);\n  font-style: italic;\n  transition: var(--transition);\n  border-right: 1px solid var(--bg-black);\n  padding: 1.4rem 4%;\n  cursor: pointer;\n}\n.navbar .navbar__brand p {\n  background-color: var(--bg-contrast);\n  padding: 0.4rem;\n  border: 1px solid var(--bg-black);\n  box-shadow: var(--shadow);\n}\n\n.navbar .navbar__burger a {\n  padding: 2rem;\n  transition: var(--transition);\n}\n\n.navbar .navbar__end a:focus,\n.navbar .navbar__burger a:focus {\n  background-color: var(--bg-contrast);\n  padding: 1rem;\n  border: 1px solid var(--bg-black);\n  box-shadow: var(--shadow);\n  font-weight: 700;\n  margin-right: 0.7rem;\n}\n\n.navbar .navbar__end a:hover {\n  background-color: var(--bg-contrast);\n  padding: 1rem;\n  border: 1px solid var(--bg-black);\n  box-shadow: var(--shadow);\n  font-weight: 700;\n}\n\n.active-hover {\n  background-color: var(--bg-contrast);\n  padding: 0.7rem;\n  border: 1px solid var(--bg-black);\n  box-shadow: var(--shadow);\n  font-weight: 700;\n}\n\n.navbar .navbar__end {\n  padding: 2rem;\n}\n\n.navbar .navbar__end a {\n  color: var(--bg-black);\n  margin: 0 0.5rem;\n  transition: var(--transition);\n  padding: 2rem 1.5rem;\n  font-size: 1.3rem;\n}\n\n#hamburgerButton {\n  display: none;\n}\n\n/* Hero Section */\n.hero {\n  display: grid;\n  grid-template-columns: 1fr 1fr; /* Membagi layout menjadi dua kolom dengan lebar yang sama */\n  align-items: center; /* Mengatur vertikal rata tengah */\n  justify-content: center;\n  gap: 20px; /* Jarak antara elemen */\n  padding: 1.4rem 7%;\n}\n\n.hero .right {\n  background-image: url(../public/images/heros/hero-image_2.jpg);\n  background-size: cover;\n  background-position: center;\n  position: relative;\n  height: 85%;\n  border-radius: 5rem;\n  margin: 7rem -7% auto 0%;\n}\n\n.hero .left {\n  margin: 0 auto;\n}\n\n.hero .left h1,\nheader .restaurant__name,\n.restaurant__info h3 {\n  font-family: \"Monoton\", cursive;\n}\n\n.hero h1 {\n  font-size: 5em;\n  color: var(--bg-black);\n  line-height: 1.2;\n}\n\n.hero h1 span {\n  color: var(--bg-contrast);\n}\n\n.hero p {\n  color: var(--bg-black);\n  font-size: 1.6rem;\n  margin-top: 1rem;\n  line-height: 1.4;\n}\n\n.hero .cta {\n  margin-top: 1rem;\n  display: inline-block;\n  padding: 2rem 3rem;\n  font-size: 1.4rem;\n  color: var(--bg-black);\n  background-color: var(--bg-contrast);\n  border: 1px solid var(--bg-black);\n  cursor: pointer;\n  transition: var(--transition);\n  box-shadow: var(--shadow);\n}\n\n.hero .cta:hover {\n  background-color: #cd934c;\n}\n.hero .cta:focus {\n  background-color: #cd934c;\n}\n/* Hero Section */\n\n/* About Section */\n.about {\n  background: var(--bg-secondary);\n}\n\n.about,\n.list {\n  padding: 8rem 7% 1.4rem;\n}\n\n.about h2,\n.list h2 {\n  color: var(--bg-black);\n  font-family: \"Monoton\", cursive;\n  text-align: center;\n  font-size: 2.6rem;\n  margin-bottom: 3rem;\n  letter-spacing: 0.3rem;\n}\n\n.about .row {\n  display: flex;\n  gap: 1.7rem;\n}\n\n.about .row .about__img {\n  flex: 1 1 45rem;\n}\n\n.about .row .about__img img {\n  width: 100%;\n  border-radius: var(--border-radius);\n}\n\n.about .row .about__content {\n  flex: 1 1 35rem;\n  padding: 0 1rem;\n}\n\n.about .row .about__content h3 span {\n  font-family: \"Monoton\", cursive;\n  font-size: 2rem;\n}\n\n.about .row .about__content h3 {\n  color: var(--bg-black);\n  font-size: 1.8rem;\n  margin-bottom: 1rem;\n}\n\n.about .row .about__content p {\n  color: var(--bg-black);\n  margin-bottom: 0.8rem;\n  font-size: 1.4rem;\n  font-weight: 400;\n  line-height: 1.6;\n  text-align: justify;\n}\n\n.list h2 {\n  margin-bottom: 1rem;\n}\n/* About Section */\n\n/* detail page */\nheader .restaurant__name {\n  margin-top: 8rem;\n  font-size: 2.5rem;\n  text-align: center;\n  color: var(--bg-black);\n  margin-bottom: 2rem;\n}\n\n.restaurant__image,\n.restaurant__info,\n.restaurant__menu,\n#resto__reviews {\n  width: 80%;\n  margin: 0 auto;\n}\n\n.restaurant__image {\n  display: block;\n  width: 60%;\n  border-radius: var(--border-radius);\n  margin-bottom: 2rem;\n}\n\n.restaurant__info {\n  padding: 2rem;\n  color: var(--bg-black);\n  margin-bottom: 2rem;\n  font-size: 1.6rem;\n  line-height: 1.4;\n  text-align: justify;\n}\n\n.restaurant__info h3 {\n  font-size: 2rem;\n  margin-bottom: 1.3rem;\n}\n\n.restaurant__menu {\n  display: flex;\n  justify-content: space-around;\n}\n\n.menu__foods,\n.menu__drinks {\n  flex: 1;\n  background-color: var(--bg-secondary);\n  border-radius: var(--border-radius);\n  box-shadow: var(--shadow);\n  margin: 10px;\n  padding: 2rem;\n  text-align: left;\n  transition: var(--transition);\n  font-size: 1.4rem;\n}\n\n.menu__foods {\n  border-radius: var(--border-radius);\n}\n\n.menu__drinks {\n  border-radius: var(--border-radius);\n}\n\n.menu__foods ul,\n.menu__drinks ul {\n  list-style-type: none;\n  padding: 0;\n  margin: 0;\n}\n\n.menu__foods h3,\n.menu__drinks h3,\n#resto__reviews h2,\n.review__form h2 {\n  color: var(--bg-black);\n  font-family: \"Monoton\", cursive;\n  font-weight: 700;\n  font-size: 1.6rem;\n  margin: 0;\n}\n\n.menu__foods hr,\n.menu__drinks hr {\n  border: none;\n  border-top: 2px solid var(--bg-black);\n  margin: 1rem 0;\n}\n\n.menu__foods li,\n.menu__drinks li {\n  display: flex;\n  align-items: center;\n  margin: 0.5rem 0;\n}\n\n.menu__foods .fa-cutlery,\n.menu__drinks .fa-coffee {\n  margin-right: 0.5rem;\n}\n\n#resto__reviews {\n  padding: 1rem;\n  background-color: var(--bg-secondary);\n  box-shadow: var(--shadow);\n  transition: background-color var(--transition);\n  margin-top: 2rem;\n  margin-bottom: 3rem;\n  border-radius: var(--border-radius);\n}\n\n#resto__reviews h2,\n.review__form h2 {\n  text-align: center;\n}\n\n.reviews {\n  list-style: none;\n  padding: 2rem;\n}\n\n.review__item {\n  background-color: var(--bg-contrast);\n  border-radius: 8px;\n  margin: 1rem 0;\n  padding: 1rem 2rem;\n  box-shadow: var(--shadow);\n  transition: background-color var(--transition);\n}\n\n.review__header {\n  display: flex;\n  justify-content: space-between;\n}\n\n.review__name {\n  font-weight: bold;\n  display: fl