rem;\n  padding: 1.3rem 0.9rem;\n}\n\nfooter .socials a:hover,\nfooter .links a:hover {\n  color: var(--bg-primary);\n}\n\nfooter .links {\n  margin-bottom: 1.4rem;\n}\n\nfooter .links a {\n  padding: 1.5rem 0.5rem;\n}\n\nfooter .credit {\n  font-size: 0.8rem;\n}\n\nfooter .credit a {\n  color: var(--bg-primary);\n  font-weight: 700;\n}\n/* Footer */\n\n/*------------------------------- Responsive -----------------------------*/\n/* Desktop */\n@media (max-width: 1366px) {\n  html {\n    font-size: 75%;\n  }\n}\n\n@media (max-width: 1199px) {\n  .hero {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    text-align: center;\n    border-bottom: none;\n  }\n\n  .hero .right {\n    background-color: rgba(0, 0, 0, 0.5);\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    filter: brightness(30%); /* Membuat gambar menjadi sedikit gelap */\n    z-index: 1;\n    border-radius: 0;\n    height: 100vh;\n    margin-top: 6rem;\n  }\n\n  .hero .left {\n    width: 100%;\n    z-index: 2;\n  }\n\n  .hero .left h1 {\n    font-size: 5em;\n    color: #fff;\n    text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);\n    line-height: 1.2;\n  }\n\n  .hero .left p {\n    color: #fff;\n    font-size: 1.6rem;\n    margin-top: 1rem;\n    line-height: 1.4;\n    font-weight: 100;\n    text-shadow: 1px 1px 3px rgba(1, 1, 3, 0.5);\n  }\n}\n/* Tablet */\n\n@media (max-width: 790px) {\n  html {\n    font-size: 62.5%;\n  }\n\n  .skip-link {\n    padding: 1.7rem 0.5rem;\n  }\n\n  #hamburgerButton {\n    display: inline-block;\n  }\n\n  .navbar .navbar__burger a {\n    padding: 2.5rem;\n  }\n\n  .navbar .navbar__end {\n    position: absolute;\n    top: 100%;\n    right: -100%;\n    background-color: var(--bg-primary);\n    width: 30rem;\n    height: 100vh;\n    transition: var(--transition);\n  }\n\n  .navbar .navbar__end.active {\n    right: 0;\n  }\n\n  .navbar .navbar__end a {\n    display: block;\n    color: var(--bg-black);\n    margin: 1.5rem;\n    pad